}


.my-custom-btn {
    border: none;
    border-radius:8px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    margin-bottom: 20px;
}

.my-custom-btn:hover {
    background-color: #45a049;
}


body {
    font-family: Arial, sans-serif;
    background-color: #f0f8ff; /* Delikatny odcień zielono-białego */
    margin: 0;
    padding: 0;
}

.container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 20px;
    background-color: white; /* Białe tło dla kontenera */
}

.header {
    background-color: #4caf50;
    color: white;
    padding: 10px 0;
    text-align: center;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    position: fixed;
    width: 100%;
    top: 0;
    left: 0;
    transition: top 0.2s;
    border-radius: 0 0 10px 10px;
}

.header h1 {
    font-size: 32px;
}

.header.scrolling {
    top: -100px;
}

.main {
    padding-top: 80px;
    padding-bottom: 60px;
}

.footer {
    background-color: #4caf50;
    color: white;
    text-align: center;
    padding: 10px 0;
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100%;
}

.recipe-search-form {
    background-color: white;
    border: 1px solid #ddd;
    padding: 20px;
    border-radius: 5px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}
